.news {

    padding-bottom: var(--section-offset);

    &__list {
        @include small-tablet {
            row-gap: var(--gap);
        }
    }

    &__item {
        grid-column: 4 span;
    }
}

.news-card {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 478px;
    background: var(--light-gray);


    &__image {
        @include mobile {
            width: 100%;
        }
    }

    &__inner {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex-grow: 1;
        padding: 16px 16px 24px;
    }

    &__title {
        display: -webkit-box;
        overflow: hidden;
        margin: 0;
        margin-bottom: 20px;
        font-weight: 400;
        font-size: 20px;
        line-height: 140%;
        color: var(--dark);
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;


        @include tablet {
            font-size: 18px;
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: auto;
        width: 100%;
    }

    &__link {
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        color: var(--accent);

        &::after {
            content: '';
            position: absolute;
            inset: 0;
        }
    }

    &__date {
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        color: var(--gray);
    }

    @include tablet {
        min-height: 430px;
    }
}

.news-wrapper {
    position: relative;

    .swiper-slide {
        height: auto;
    }
}


.news-btn {
    cursor: pointer;
    position: absolute;
    top: 50%;
    z-index: 50;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    box-shadow: 10px 0px 23px -5px rgba(34, 60, 80, 0.2);
    background-color: var(--light);
    transform: translateY(-50%);

    svg {
        width: 24px;
        fill: var(--dark);
    }

    &--prev {
        left: -30px;
    }

    &--next {
        right: -30px;
    }
}