.present {
    &__map {
        width: calc(100% - 29px);
    }

    svg {
        width: 100%;
        display: block;
        object-fit: cover;
    }

    path {
        fill: var(--third);
        transition: fill .3s;
    }

    &__text {
        fill: var(--dark) !important;
    }

    &__link {
        position: relative;
        
        &::after {
            content: attr(aria-label);
            position: absolute;
            top: 0;
            left: 0;
            font-weight: 400;
            font-size: 12px;
            line-height: 120%;
            color: var(--dark);
        }

        @include hover {
            path {
                fill: var(--light-gray);
                transition: fill .5s;
            }
        }
    }

    @include tablet {
        display: none;
    }
}